define('view/pieces/segs/elements/demo-tip',[],function(){
	function Demo(){
		$("button.btn-primary").click(function(){
			
			showTip({
				content:"10030",
				closeCallback:function(){//tip
					console.log(" primary");
				}
			});
			
		});
		
		$("button.btn-warning").click(function(){
			
			showTip({
				type:"warning",
				content:"100",
				icon:"<i class='glyphicon glyphicon-alert'></i>",
				close:true,
				closeCallback:function(){//tip
					console.log(" waning");
				}				
			});
			
		});
		
		$("button.btn-success").click(function(){
			
			showTip({
				type:"success",
				content:"<button class='btn btn-primary btn2 btn-sm'></button>",
				icon:"<i class='glyphicon glyphicon-flag'></i>",
				close:true,
				closeCallback:function(){//tip
					console.log(" success");
				},
				clickCallback:function(ta,tip){//x tip
					if(ta.tagName=="BUTTON" && $(ta).hasClass("btn2")){
						console.log("tip");
					}
				}
			});
			
		});	
		
		$("button.btn-danger").click(function(){
			
			showTip({
				through:false,
				type:"danger",
				content:"",
				icon:"<i class='glyphicon glyphicon-flag'></i>",
				close:true
			});
			
		});	
		
		$("button.btn-success2").click(function(){
			
			showTip({
				time:null,
				through:false,
				type:"danger",
				content:"wait",
				icon:"<i class='glyphicon glyphicon-home'></i>",
				close:true
			});
			
		});	
		
		
		$("button#temp-btn").click(function(){
			
			showTip({
				time:null,//  30 30s
				type:"danger",
				bind:$(".temp-window"),//DOM jquery
				content:"",
				icon:"<i class='glyphicon glyphicon-flag'></i>",
				close:true
			});
			
		});		
		
	}
	return Demo;
});
